.Container{
  display: flex;
  justify-content: center;
  gap: 20px;

}
body{
  background-image: linear-gradient(rgb(54, 16, 100),rgb(132, 77, 156)) ;
}

h2{
  margin-top: 0px;
  margin-bottom: 0px;

}
p{
    margin-top: 1px;
    margin-bottom: 1px;

  
}
.Container .allimg{
  text-align: center;
  box-shadow: 3px 3px 5px rgb(120, 110, 110);
  border: 1px solid gray;
  border-radius: 10px;

}
.img{
  object-fit: cover;
  width: 300px;
  border-radius: 10px;
}
.allimg p{
  margin: 5px 0px;
  font-size: large;
}
.allimg button{
  margin: 3px ;
  padding: 7px 22px ;
  border-radius: 10px;
  font-weight:900;
  border: none;
  background-color:  #623686;
  font-size: 20px;
  font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  margin-bottom: 10px;
}
.allimg{
  color: #ccc;
}

.allimg button:hover{
  background-color: #fff;
}



  .Carddisplay {
    max-width: 400px;
    margin: 0 auto;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    text-align: center;
    background-color: rgb(83, 50, 125);
    background-size: 400% 400%;
    animation: gradientAnimation 10s ease infinite;
  }
  
  @keyframes gradientAnimation {
    0% {
      background-position: 0% 50%;
    }
    50% {
      background-position: 100% 50%;
    }
    100% {
      background-position: 0% 50%;
    }
  }
  
  .Carddisplay h1 {
    font-size: 24px;
    margin-bottom: 20px;
    color: #fff; /* Updated to ensure the text is visible on the gradient background */
  }
  
  .Carddisplay h1 span {
    margin: 0 10px;
    cursor: pointer;
    font-size: 18px;
    font-weight: bold;
    padding: 5px 10px;
    border: 1px solid  #8539c3;
    border-radius: 4px;
    background-color:  #8539c3;
    transition: background-color 0.3s ease;
  }
  
  .Carddisplay h1 span:hover {
    background-color: #d4d4d4;
  }
  
  .Carddisplay button {
    margin-top: 20px;
    padding: 10px 20px;
    font-size: 16px;
    color: #fff;
    background-color: #8539c3;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .Carddisplay button:hover {
    background-color: #4c327e;
  }
  
.shop{
  text-align: center;
  color: #ccc;
}
